.categories {
  &__catalog {
    display: flex;
    width: 100%;
    height: 800px;
    @include tablet {
      height: 600px;
    }
  }
  &__col-1 {
    flex-direction: column;
    flex: 1 0 auto;
    margin-right: 16px;
    @include tablet {
      margin-right: 10px;
    }
  }

  &__item {
    filter: drop-shadow(0px 4px 35px rgba(0, 0, 0, 0.25));
    background-color: white;
    position: relative;
    transition: all 0.2s linear;
    &:hover {
      transform: translateY(5px);
      background-color: rgb(228, 228, 228);

      // transition: all 0.2s linear;
    }
  }

  &__laptops {
    flex-grow: 1;
    .col__content {
      right: 7%;
      bottom: 0;
      max-width: 30%;
    }
    .col__img {
      max-width: 77%;
      margin-left: 6.5%;
      height: auto;
    }
  }
  &__watch {
    height: 35%;
    .col__content {
      right: 7%;
      bottom: 32%;
      max-width: 30%;
    }
    .col__img {
      margin-left: 6.5%;
      height: 100%;
      img {
        max-width: 100%;
        max-height: 100%;
      }
    }
  }

  &__vertical-item {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 18% 0;
    &:first-child {
      flex-direction: column-reverse;
    }
    display: flex;
    justify-content: center;
    align-items: center;
    .col__content {
      position: initial;
      margin-top: 40px;
      max-width: 80%;
    }
    .col__text {
      text-align: center;
    }
  }

  &__tablets {
    .col__content {
      margin-bottom: 40px;
    }
  }
}

// Categories

// Col
.col {
  height: 100%;
  display: flex;
  gap: 16px;
  @include tablet {
    gap: 10px;
  }
  &__content {
    position: absolute;
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    align-items: center;
  }
  &__title {
    font-family: "Inter";
    font-style: normal;
    font-weight: 400;
    font-size: 15px;
    line-height: 1.2;
    text-align: right;
    color: #000000;
    @include tablet {
      font-size: 13px;
    }
  }
  &__text {
    font-family: "Readex";
    font-style: normal;
    font-weight: 400;
    font-size: 32px;
    line-height: 1.2;
    text-align: right;
    color: #000000;
    @include tablet {
      font-size: 24px;
    }
  }
}
