.section-search {
  .section__title {
    margin-bottom: 50px;
  }
}

.search {
  width: 680px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.search {
  // .search__imput

  &__input {
    flex-grow: 1;
    ::placeholder {
      color: black;
    }
  }
  &__img {
    height: 25px;
    margin-right: 15px;
  }
  // .search__keywords

  &__keywords {
    display: flex;
    flex-wrap: wrap;
    max-width: 100%;
    justify-content: center;
  }

  // .search__keyword

  &__keyword {
    padding: 12px 22px;
    border: 1px solid #c5c5c5;
    border-radius: 100px;
  }
}
.input__wrapper {
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
  align-items: center;
  width: 650px;
  height: 60px;
  padding: 0 15px;
  background-color: #f8f9fa;
  border: 1px solid #c5c5c5;
  border-radius: 100px;
  margin-bottom: 18px;
}
