.ideas {
  // .ideas__img
  display: flex;
  justify-content: space-between;
  &__img {
    img {
      width: 620px;
      height: 420px;
      border-radius: 16px;
    }
  }

  // .ideas__paragraphs

  &__paragraphs {
    padding-left: 35px;
    font-family: "Inter";
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 30px;
    color: #000000;
    p {
      margin-bottom: 23px;
    }
  }

  // .ideas__link

  &__link {
    margin-top: 74px;
    padding-right: 30px;
    font-family: "Inter";
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 29px;
    /* identical to box height, or 120% */
    background: url("../../img/arrow-best.png") no-repeat right;
    text-transform: capitalize;

    color: #000000;
  }
}
