.section {
  padding-top: 70px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  @include tablet {
    padding-top: 50px;
  }
  &__title {
    font-family: "Readex";
    font-style: normal;
    font-weight: 400;
    font-size: 42px;
    line-height: 1.2;
    text-align: center;
    color: #000000;
    @include tablet {
      font-size: 36px;
    }
  }
  &__subtitle {
    margin-top: 8px;
    font-family: "Readex";
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 1.2;
    text-align: center;
    color: #000000;
    max-width: 600px;
    margin-bottom: 56px;
    @include tablet {
      font-size: 13px;
      margin-bottom: 30px;
    }
  }
}
