.footer {
  margin-top: 19px;
  width: 100%;
  background-color: #f8f9fa;
  // .footer_copyright

  &_copyright {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    padding: 0 45px;
    margin-top: 38px;
    border-top: 1px solid #dce5e2;
    height: 78px;
  }
  .footer__copyright__link {
    margin-bottom: 4px;
    font-family: "Inter";
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 22px;
    padding-right: 40px;
    /* identical to box height, or 120% */
    background: url("../../img/arrow-best.png") no-repeat right;
    color: #000000;
    span {
      font-family: "Readex";
    }
  }
  // .footer__inner

  &__inner {
    display: flex;
    padding-top: 45px;
  }

  // .footer__content

  // .footer__info

  &__info {
    width: 30%;
    &__text {
      max-width: 300px;
      margin-top: 18px;
    }
  }

  // .footer__logo

  &__logo {
  }

  // .footer__nav

  &__nav {
    flex-grow: 1;
    display: flex;
    justify-content: space-between;
  }

  // .footer__list

  &__list {
    display: flex;
    flex-direction: column;
    &__title {
      font-family: "Readex";
      font-style: normal;
      font-weight: 400;
      font-size: 22px;
      line-height: 28px;
      color: #000000;
      margin-bottom: 20px;
    }
    &__item {
      font-family: "Inter";
      font-style: normal;
      font-weight: 400;
      font-size: 17px;
      line-height: 22px;
      color: #000000;
      margin-bottom: 19px;
    }
  }

  // .footer__copyright-link

  &__copyright-link {
  }
}
