.product-slider {
  width: 100%;
  height: 550px;
  border-radius: 32px;
  @include tablet {
    height: 400px;
  }
}
.swiper-wrapper {
  display: flex;
  width: 100%;
  height: 100%;
}
.slide {
  position: relative;
  display: flex;
  padding-left: 50px;
  background-color: #dce5e2;

  &__content {
    height: 100%;
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  &__title {
    font-family: "Readex";
    font-style: normal;
    font-weight: 400;
    font-size: 58px;
    line-height: 1.2;
    margin-bottom: 19px;
    @include tablet {
      font-size: 40px;
    }
    /* or 120% */

    color: #000000;
  }
  &__subtitle {
    font-family: "Inter";
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 1.2;
    color: #000000;
    @include tablet {
      font-size: 14px;
      max-width: 350px;
    }
  }
  &__img {
    position: absolute;
    bottom: 0;
    right: 0;
    img {
      @include tablet {
        height: 400px;
      }
    }
  }
  &__btn {
    max-width: 150px;
    @include tablet {
      max-width: 120px;
    }
  }
}

.swiper-pagination-bullet {
  min-width: 194px;
  max-width: 25%;
  height: 2px;
  border-radius: 0;
  @include tablet {
    min-width: 120px;
  }
  &-active {
    background: #000;
  }
}
.swiper-button {
  margin-top: 0;
  transform: translateY(-50%);
  color: black;
  &::after {
    font-size: 30px;
  }
}
