.selled {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  &__item {
    position: relative;
    display: flex;
    flex-direction: column;
    max-width: 23.3%;
    min-height: 396px;
    transition: all 0.15s linear;
    &__img {
      min-height: 270px;
    }
    &__title {
      font-family: "Readex";
      font-style: normal;
      font-weight: 400;
      font-size: 22px;
      line-height: 28px;
      color: #000000;
      margin-bottom: 9px;
    }
    &__text {
      font-family: "Inter";
      font-style: normal;
      font-weight: 400;
      font-size: 14px;
      line-height: 17px;
      color: #4b4b4b;
    }
    &__info {
      position: absolute;
      bottom: 0;
      width: 100%;
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
    &__price {
    }
    &__btn {
      margin-top: 0;

      min-height: 30px;
      width: 100px;
    }
    &:hover {
      scale: 1.1;
      // -webkit-box-shadow: 0px 2px 100px 2px rgba(0, 0, 0, 0.08);
      // -moz-box-shadow: 0px 2px 100px 2px rgba(0, 0, 0, 0.08);
      // box-shadow: 0px 2px 100px 2px rgba(0, 0, 0, 0.08);
    }
  }
}
