.sing-up {
  margin-top: 61px;
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 72%;
  margin: 61px auto 0 auto;
  background-color: #dce5e2;
  border-radius: 16px;
  // .sing-up__inner
  .section__title {
    margin-top: 47px;
  }
  .section__subtitle {
    margin-bottom: 14px;
  }
  &__inner {
    display: flex;
    flex-direction: column;
  }

  // .sing-up__img

  &__img {
    display: flex;
    justify-content: center;
  }

  // .sing-up__input-wrapper

  &__input-wrapper {
    display: flex;
    justify-content: space-between;
    margin-top: 14px;
    margin-bottom: 43px;
    // width: 42%;
    height: 50px;
    border: 1px solid black;
    border-radius: 25px;
    overflow: hidden;
    padding-left: 15px;
  }

  // .sing-up__input
  &__btn {
    height: 100%;
    width: 28%;
    border-left: 2px solid black;
    color: black;
    transition: all 0.2s linear;
    &:hover {
      color: white;
      background-color: black;
    }
  }
  &__input {
    width: 100%;
  }
}
