  align-items: center;
}
.header__logo {
  height: 21px;
}
@keyframes fadeInUp {
  from {
    opacity: 0;
    top: -50px;
  }
  to {
    opacity: 1;
    top: 0;
  }
}

.fixed-header {
  position: fixed;
  width: 100%;
  left: 0;
  top: 0;
  z-index: 4;
  animation-name: fadeInUp;
  animation-duration: 1s;
  background-color: #f1f1f1;
}

.nav {
  display: flex;
}
@media (max-width: 1024px) {
  .nav {
    flex-direction: column;
    position: fixed;
    top: 0;
    left: 0;
    overflow-x: hidden;
    overflow-y: auto;
    z-index: 5;
    height: 100vh;
    box-shadow: 0 4px 25px rgba(0, 0, 0, 0.9);
    align-content: center;
    max-width: 280px;
    width: 100%;
    background: #fff;
    -webkit-transition: 0.3s ease;
    transition: 0.3s ease;
    transform: translateX(-150%);
    padding: 15px;
  }
}
.nav__list {
  display: flex;
  list-style: none;
}
@media (max-width: 1024px) {
  .nav__list {
    flex-direction: column;
    padding-top: 100px;
    justify-content: start;
  }
}
.nav__item {
  margin-left: 33px;
  position: relative;
}
@media (max-width: 1024px) {
  .nav__item {
    margin-left: 0;
    margin-bottom: 25px;
    font-size: 30px;
  }
}
.nav__item:first-child {
  margin-left: 0;
}
.nav__link {
  padding-bottom: 3px;
  position: relative;
  font-family: "Inter";
  font-weight: 400;
  font-size: 14px;
  text-transform: uppercase;
  color: #000000;
}
.nav__link::after {
  position: absolute;
  bottom: 0;
  left: 50%;
  content: "";
  width: 0%;
  height: 1px;
  color: black;
  background: currentColor;
  transition: all 0.3s ease-out;
}
.nav__link:hover::after {
  width: 100%;
  left: 0%;
}
.nav__link:hover {
  scale: 1.03;
  transition: all 0.15s linear;
}
@media (max-width: 1024px) {
  .nav__link {
    font-family: var(--font-family);
  }
}
.nav_burger_logo {
  display: none;
  width: 170px;
  height: auto;
  padding-top: 15px;
}
.nav_burger_info {
  display: none;
}

.vis {
  transform: translateX(0%);
}

.r-header {
