.header {
  position: static;
  display: flex;
  min-width: 100%;

  align-items: center;
  height: var(--header--heights);
  --header--heights: 70px;
  @include tablet {
    --header--heights: 60px;
  }
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__logo {
    height: 21px;
  }
  @keyframes fadeInUp {
    from {
      opacity: 0;
      top: -50px;
    }

    to {
      opacity: 1;
      top: 0;
    }
  }
}
.fixed-header {
  position: fixed;
  width: 100%;
  left: 0;
  top: 0;
  z-index: 4;
  animation-name: fadeInUp;
  animation-duration: 1s;
  background-color: #f1f1f1;
}
.nav {
  display: flex;

  @include tablet {
    flex-direction: column;
    position: fixed;
    top: 0;
    left: 0;
    overflow-x: hidden;
    overflow-y: auto;
    z-index: 5;
    height: 100vh;
    box-shadow: 0 4px 25px rgba(0, 0, 0, 0.9);
    align-content: center;
    max-width: 280px;
    width: 100%;
    background: #fff;
    -webkit-transition: 0.3s ease;
    transition: 0.3s ease;
    transform: translateX(-150%);
    padding: 15px;
  }
  &__list {
    display: flex;
    list-style: none;

    @include tablet {
      flex-direction: column;
      padding-top: 100px;
      justify-content: start;
    }
  }
  &__item {
    margin-left: 33px;
    position: relative;

    @include tablet {
      margin-left: 0;
      margin-bottom: 25px;
      font-size: 30px;
    }
    &:first-child {
      margin-left: 0;
    }
  }
  &__link {
    padding-bottom: 3px;
    position: relative;
    font-family: "Inter";
    font-weight: 400;
    font-size: 14px;
    text-transform: uppercase;
    color: #000000;
    &::after {
      position: absolute;
      bottom: 0;
      left: 50%;
      content: "";
      width: 0%;
      height: 1px;
      color: black;
      background: currentColor;
      transition: all 0.3s ease-out;
    }
    &:hover::after {
      width: 100%;
      left: 0%;
    }
    &:hover {
      scale: 1.03;
      transition: all 0.15s linear;
    }
    @include tablet {
      font-family: var(--font-family);
    }
  }

  &_burger_logo {
    display: none;
    width: 170px;
    height: auto;
    padding-top: 15px;
  }
  &_burger_info {
    display: none;
  }
}
.vis {
  transform: translateX(0%);
}
.r-header {
  display: flex;
  justify-content: end;
  min-width: 70px;
  .search__block {
    display: flex;
    margin-right: 22px;
    input {
      display: none;
      max-width: 350px;
    }
  }
}
.burger {
  display: none;
  position: relative;

  @include tablet {
    display: block;
  }

  @include burger;
}

.headeroverlay {
  position: absolute;
  width: 100vw;
  height: 100vh;
  z-index: 3;
  background: rgba(0, 0, 0, 0.8);
  cursor: pointer;
  display: none;
  opacity: 0;
  transition: all 1s;
}
